.details {
  margin: auto;
  width: 300px;
  background-color: #cecece;
  padding: 15px;
}
.delete {
  width: 150px;
  margin: auto;
  margin-top: 10px;
  background-color: #ff726f;
  border: 1px solid black;
  padding: 5px;
}
.delete:hover {
  transform: scale(1.2);
}
.editBtn {
  margin: auto;
  width: 60px;
  margin-top: 10px;
  background-color: lightblue;
  border: 1px solid black;
  padding: 5px;
}
.editBtn:hover {
  transform: scale(1.2);
}
