.App {
  text-align: center;
}
.addbtn {
  padding: 5px;
  background-color: white;
  border: 1px solid black;
  width: 50px;
  font-size: 2rem;
  margin-bottom: 10px;
  transition: ease-in 0.3s;
}
.addbtn:hover {
  border-color: white;
  background-color: black;
  color: white;
  transform: rotate(180deg);
  transition: ease-out 0.5s;
}
.newStudent {
  display: flex;
  flex-direction: column;
  width: 200px;
  margin: auto;
  border: 1px solid black;
  padding: 5px;
  background-color: rgba(2, 2, 2, 0.3);
  margin-bottom: 15px;
}
.Add {
  width: 200px;
  margin: auto;
  margin-top: 10px;
  background-color: palegreen;
  border: 1px solid black;
  padding: 5px;
}
.Add:hover {
  transform: scale(1.2);
}
